:root {
  --drop-sombra: rgba(128, 125, 125, 0.89);
}

.background {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  overflow: hidden;
  margin: 0 auto;
  height: 100%;
  width: 100%;
}

.blob {
  position: absolute;
  width:200%;
  -webkit-filter: drop-shadow(0px 0px 3px rgba(128, 125, 125, 0.89));
          filter: drop-shadow(0px 0px 3px rgba(128, 125, 125, 0.89));
  -webkit-filter: drop-shadow(0px 0px 3px var(--drop-sombra));
          filter: drop-shadow(0px 0px 3px var(--drop-sombra));

}
.blob-green {

  transform: translate(-48%, 30%);
  
}
.blob-red {

  transform: translate(50%, 40%);
}
.blob-yellow {
  transform: translate(0, -60%);
}

form {
  overflow: auto;
  display: flex;
  align-items: center;

  margin: 0 auto;
  width: 100%;
  margin-bottom: 25px;
}
form img {
  padding-left: 4px;
  padding-bottom: 3px;
  position: absolute;
}
form input {
  width: 85%;
  border: none;
  border-bottom: 1px solid black;
  padding: 5px 5px 5px 30px;
  background: rgba(255, 255, 255, 0);
}

